# <script lang="coffee">
moment = require "moment"
debug=require("debug")("messages")
module.exports=
  components:
    Navbar:require("../../components/navbar.vue")
  computed:
    avatarUrl:->
      @currentUser.getAvatarUrl()
  filters:
    calendarTime:(val)->
      # val = new Date("Sun May 15 2016 23:45:57 GMT+0800")
      moment(val).calendar null,
        sameDay: 'HH:mm'
        lastDay: '昨天'
        lastWeek: 'dddd'
        sameElse: 'YYYY-MM-DD'
  vuex:
    getters:
      currentUser:(state)->
        state.accounts.currentUser
      messagesList:(state)->
        state.messages.messagesList
    actions:
      messageReaded:({dispatch},item)->
        debug '消息已阅。'
        dispatch("MESSAGE_READED",item)
# </script>



